* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Roboto Mono', monospace;
}

button {
  font-family: 'Roboto Mono', monospace;
}

body {
  height: 100vh;
  background: url(/react-stopwatch/static/media/bg2.dabf04ea.jpg) center;
  background-size: cover;
  background-position: fixed;
}

.header {
    background-color: rgba(32, 35, 42, 0.7);
    color: white;
    text-align: center;
    padding: 15px;
    font-size: 18px;
    letter-spacing: 2px;
}
.stopwatch {
    color: white;
}
.buttons {
    display: flex;
    justify-content: space-around;
    max-width: 400px;
    margin: 30px auto;
}

button {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: none;
    background-color: deepskyblue;
    color: white;
    font-size: 24px;
    position: relative;
    cursor: pointer;
    transition: .7s;
}

button svg {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

@media(min-width: 1024px) {
    button:hover {
        background-color: #FF511A;
    }
}
.result {
    background-color: rgba(0, 0, 0, 0.5);
    width: 80%;
    max-width: 250px;
    margin: 40px auto;
    text-align: center;
    padding: 20px;
    font-size: 44px;
    border: 2px solid black;
    box-shadow: 0 0 5px black
}
.resultsTable {
    background-color: rgba(0, 0, 0, 0.5);
    width: 90%;
    max-width: 450px;
    margin: 30px auto 10px auto;
    text-align: center;
    border: 2px solid black;
    box-shadow: 0 0 5px black
}

.resultsTable h2 {
    margin-top: 10px;
    font-size: 24px;
}

.resultsTable h2::after {
    content: '';
    display: block;
    width: 60%;
    margin: 0 auto;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.5);
}

.resultsTable li {
    list-style-type: none;
    font-size: 20px;
    margin: 20px;
}

.resultsTable li::after {
    content: '';
    display: block;
    width: 60%;
    margin: 10px auto;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.5);
}
